.edd-rbm-repeater-content, .edd-fields-field-options {

	margin-bottom: 32px; // Same Margin as the margin from the top
	padding-top: 2rem;
	overflow-x: hidden;
	
	max-width: 91%; // On Small screens this overflows to the right somewhat
	
	@media #{$medium-up} {
		max-width: none;
	}

	table.widefat {
		background: transparent;
		border: 0;
		box-shadow: none;
	}

	input.regular-text, textarea, select, div.wp-picker-container {
		width: 100%;
	}

	select.required.edd-fields-chosen {
		display: block !important; // Ensure that our validation works correctly
		position: absolute;
		clip: rect(0,0,0,0);
	}

	div.chosen-container {
		width: 100% !important;   
	}

	td {

		vertical-align: top;

		&.hidden {
			display: none !important;
		}

	}

	.validation-error {
		color: #D8000C;
	}

}