.edd-fields-draghandle-anchor {
  display: inline-block;
  background-repeat: no-repeat;
  vertical-align: middle;
  top: -0.15em;
  position: relative;
  margin: 4px 0 0 0;
  width: 20px;
  height: 20px;
  cursor: move;
  color: #c2c1c1;
}

table.widefat {

  background: transparent;
  border: 0;
  box-shadow: none;

  thead {

    tr {

      display: block;

      th {

        display: inline-block;
        max-width: none !important; // Plz no, EDD
        overflow: visible;

        div {

          display: inline-block;
          vertical-align: middle;

        }

      }

      .edd-rbm-repeater-controls {
        text-align: right;
      }

    }

  }

}

.edd-repeater-defaults {
  float: right;
}

.button.button-danger {
		
	background: #ff7151;
	border-color: #c6573e;
	color: #fff;

	&:hover {
		border-color: darken( #c6573e, 15% );
	}

}

& > .edd-rbm-repeater-list {
  @include repeater-list;
}

.edd-rbm-repeater-item {

  background: #f5f5f5;

  &.deleting {

    background: #e5e5e5;
    color: #a6a6a6;

  }

  .repeater-header {

    border: 1px #ddd;
    border-top-style: solid;
    border-bottom-style: solid;
    padding: 20px 0px 20px 10px;

    th {
      vertical-align: middle;
      width: 100%;
      padding: 0;
      border: none;
    }

    .status-indicator {

      line-height: 1.2;

      &.active {
        color: #4F8A10;
      }

      &.inactive {
        color: #D8000C;
      }

    }

    .edd-rbm-repeater-controls {

      text-align: right;

      @media #{$medium-up} {
        float: right; // No floating on small screens, we use text-align instead
        text-align: center;
      }

      margin-right: 10px;

    }

  }

  &:first-of-type {
    .repeater-header {
      border-top-style: none;
    }
  }

  &:last-of-type {
    .repeater-header {
      border-bottom-style: none;
    }
  }

}